<template>

    <div class="table">
      <!-- <p class="title">
        学生成绩详细查询
      </p> -->


      <section class="content-el">
        <div>
        <label style="color: #909399;
      font-weight: 500;">课程名称：</label>
      <el-input  placeholder="请输入课程名称" style="width: 200px;margin-right: 30px;" v-model="subject" clearable>
        </el-input>
        <label style="color: #909399;
      font-weight: 500;">学生姓名：</label>
        <el-input  placeholder="请输入学生姓名" style="width: 200px;margin-right: 30px;" v-model="studentname" clearable>
        </el-input>
        <label style="color: #909399;
      font-weight: 500;" >考试日期：</label>
      <el-date-picker
        type="date"
        placeholder="选择日期" style="margin-right: 30px;" v-model="date" value-format="yyyy-MM-dd">
      </el-date-picker>
      <el-button type="primary" @click="onSubmit">查询</el-button>
      <el-button type="primary" @click="exportExcel">导出excel</el-button>
      </div>
        <el-table ref="filterTable" :data="error" v-loading="loading">
          <el-table-column
            prop="examDate"
            label="考试日期"
            sortable
            width="150">
          </el-table-column>
            <el-table-column label="学生姓名" width="100"  >
            <template slot-scope="scope">

              {{ scope.row.studentName}}
            </template>
          </el-table-column>
          <el-table-column
            prop="subject"
            label="课程名称"
            width="150"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag>{{scope.row.subject}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            prop="title"
            label="题目"
            width="350"
            filter-placement="bottom-end">
          </el-table-column>
          <el-table-column prop="yourAnswer" label="你的答案" width="150">
          </el-table-column>
          <el-table-column prop="rightAnswer" label="正确答案" width="150">
            <template slot-scope="scope">
                <span style="color:red">{{ scope.row.rightAnswer }}</span>
            </template>

          </el-table-column>
        </el-table>
        <el-row type="flex" justify="center" align="middle" class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pagination.current"
            :page-sizes="[4,6,8,10]"
            :page-size="pagination.size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagination.total">
          </el-pagination>
        </el-row>
      </section>
    </div>
  </template>


  <script>
  let map=new Map()
  export default {
    data() {
      return {
        pagination: { //分页后的留言列表
          current: 1, //当前页
          total: null, //记录条数
          size: 10 //每页条数
        },
        loading: false, //加载标识符
        error: [], //错题
        filter: null, //过滤参数
        date:'',
        subject:'',
        name:'',
        studentname:'',

      }
    },
    created() {
      let errorflag=sessionStorage.getItem("error")

      if(errorflag==1)
      {
        sessionStorage.removeItem("error")
        this.fromTable()
      }
      else{
        this.onSubmit()
      }
      this.loading = true //数据加载则遮罩表格
    },
    methods: {
      exportExcel(){
        var studentId = null
        studentId = sessionStorage.getItem("student")
      this.loadingExcel=true
      this.$axios({
            headers: {
                'Content-Type': 'application/json'
            },
            url:'/${process.env.otherApi}/export',
            method:'post',
            responseType:'blob',
            data:{
              studentId:studentId,
              subject:this.subject,
              date:this.date
            }
            }).then(res=>{
              this.loadingExcel=false
              let blob = new Blob([res.data], { type: "application/xlsx" });

                let url = window.URL.createObjectURL(blob);
                const link = document.createElement("a"); // 创建a标签
                link.href = url;
                if(studentId==null)
                  link.download = 全部+this.subject+"的错题详情"+".xlsx"; // 重命名文件
                else
                  link.download=studentId+this.subject+"的错题详情"+".xlsx"; // 重命名文件
                link.click();
                URL.revokeObjectURL(url);

                this.$message({
                  type:'success',
                  message:'导出成功'
                })
          }).catch(()=>{
            this.loadingExcel=false
            this.$message({
                  type:'error',
                  message:'请求超时'
            })
          })
    },
      getScore() {
        let studentId = this.$jwtDecode(sessionStorage.getItem("token")).id
        this.$axios(`/${process.env.examApi}/error/${this.pagination.current}/${this.pagination.size}/${studentId}`).then(res => {
          if(res.data.code == 200) {
            this.loading = false //数据加载完成去掉遮罩
            this.error = res.data.data.records
            this.pagination = {...res.data.data}
            let mapVal = this.error.map((element,index) => { //通过map得到 filter:[{text,value}]形式的数组对象
              let newVal = {}
              newVal.text = element.answerDate
              newVal.value = element.answerDate
              return newVal
            })
            let hash = []
            const newArr = mapVal.reduce((item, next) => { //对新对象进行去重操作
              hash[next.text] ? '' : hash[next.text] = true && item.push(next);
              return item
            }, []);
            this.filter = newArr
          }
        })
      },
      //改变当前记录条数
      handleSizeChange(val) {
        this.pagination.size = val
        this.onSubmit1()

      },
      //改变当前页码，重新发送请求
      handleCurrentChange(val) {
        this.pagination.current = val
        this.onSubmit1()

      },
      formatter(row, column) {
        return row.address;
      },
      filterTag(value, row) {
        return row.tag === value;
      },
      filterHandler(value, row, column) {
        const property = column["property"];
        return row[property] === value;
      },
      onSubmit(){
        //发请求然后换数据
        if(this.studentname==''||this.studentname==null)
          {
            sessionStorage.removeItem("student")
            sessionStorage.removeItem("studentName")
          }
        this.pagination.current=1
        this.$axios({
              headers: {
                  'Content-Type': 'application/json'
              },
              url:'/${process.env.examApi}/error/'+this.pagination.current+'/'+this.pagination.size,
              method:'put',
              data:{
                studentName:this.studentname,
                subject:this.subject,
                date:this.date

              }
              }).then(res=>{
                this.loading = false //数据加载完成去掉遮罩
                this.studentname=this.studentname
                this.subject=this.subject,
                this.date=this.date
            this.error = res.data.data.records


            this.pagination = {...res.data.data}
            let mapVal = this.error.map((element,index) => { //通过map得到 filter:[{text,value}]形式的数组对象
              let newVal = {}
              newVal.text = element.answerDate
              newVal.value = element.answerDate
              return newVal
            })
            let hash = []
            const newArr = mapVal.reduce((item, next) => { //对新对象进行去重操作
              hash[next.text] ? '' : hash[next.text] = true && item.push(next);
              return item
            }, []);
            this.filter = newArr
            })

      },
      onSubmit1(){
        //发请求然后换数据
        if(this.studentname==''||this.studentname==null)
          {
            sessionStorage.removeItem("student")
            sessionStorage.removeItem("studentName")
          }
        this.$axios({
              headers: {
                  'Content-Type': 'application/json'
              },
              url:'/${process.env.examApi}/error/'+this.pagination.current+'/'+this.pagination.size,
              method:'put',
              data:{
                studentName:this.studentname,
                subject:this.subject,
                date:this.date

              }
              }).then(res=>{
                this.loading = false //数据加载完成去掉遮罩
                this.studentname=this.studentname
                this.subject=this.subject,
                this.date=this.date
            this.error = res.data.data.records


            this.pagination = {...res.data.data}
            let mapVal = this.error.map((element,index) => { //通过map得到 filter:[{text,value}]形式的数组对象
              let newVal = {}
              newVal.text = element.answerDate
              newVal.value = element.answerDate
              return newVal
            })
            let hash = []
            const newArr = mapVal.reduce((item, next) => { //对新对象进行去重操作
              hash[next.text] ? '' : hash[next.text] = true && item.push(next);
              return item
            }, []);
            this.filter = newArr
            })

      },
      fromTable(){
        let studentId = null;
        if(sessionStorage.getItem("student")!=null)
          studentId=sessionStorage.getItem("student");
        if(sessionStorage.getItem("date")!=null){
        this.date=sessionStorage.getItem("date")
        this.subject=sessionStorage.getItem("sub")
      }
        this.$axios({
              headers: {
                  'Content-Type': 'application/json'
              },
              url:'/${process.env.examApi}/error/'+this.pagination.current+'/'+this.pagination.size,
              method:'post',
              data:{
                studentId:studentId,
                subject:this.subject,
                date:this.date
              }
              }).then(res=>{
                this.loading = false //数据加载完成去掉遮罩
                this.studentname=sessionStorage.getItem("studentName")
                sessionStorage.removeItem("sub")
                sessionStorage.removeItem("date")

            this.error = res.data.data.records
            this.pagination = {...res.data.data}
            let mapVal = this.error.map((element,index) => { //通过map得到 filter:[{text,value}]形式的数组对象
              let newVal = {}
              newVal.text = element.answerDate
              newVal.value = element.answerDate
              return newVal
            })
            let hash = []
            const newArr = mapVal.reduce((item, next) => { //对新对象进行去重操作
              hash[next.text] ? '' : hash[next.text] = true && item.push(next);
              return item
            }, []);
            this.filter = newArr
            })
      }
    }
  };
  </script>

  <style lang="less" scoped>

  .pagination {
    padding-top: 20px;
  }
  .table {
    width: 1250px;
    margin: 0 auto;
    margin-left: 50px;
    .title {
      margin: 20px;
    }
    .content-el {
      background-color: #fff;
      padding: 20px;
    }
  }
  </style>



